/**
 * Table
 */

table, th {
    width: 100%;
    table-layout: fixed;
    margin: 24px;
    text-align: center;
    border-bottom: 1px solid #eee;
    border-collapse: collapse;
line-height: 25px;
letter-spacing: 1px;
}
