// Colors are drawn from the Deep Autumn palette: https://www.color-hex.com/color-palette/1015101

/**
 * Basic styling
 */

* {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}


body {
    font: $base-font-weight #{$base-font-size}/#{$base-line-height} $base-font-family;
    color: $text-color;
    background-color: $background-color;
    -webkit-text-size-adjust: 100%;
    -webkit-font-feature-settings: "kern" 1;
    -moz-font-feature-settings: "kern" 1;
    -o-font-feature-settings: "kern" 1;
    font-feature-settings: "kern" 1;
    font-kerning: normal;
}



/**
 * Set `margin-bottom` to maintain vertical rhythm
 */
h1, h2, h3, h4, h5, h6,
p, blockquote, pre,
ul, ol, dl, figure,
%vertical-rhythm {
    margin-bottom: $spacing-unit / 2;
}



/**
 * Images
 */
img {
    display: block;
    margin: 0 auto;
    max-width: 100%;
}



/**
 * Figures
 */
figure > img {
    display: block;
}

figcaption {
    font-size: $small-font-size;
}



/**
 * Lists
 */
ul, ol {
    margin-left: $spacing-unit;
    color: #666;
    letter-spacing: 1px;
}

ul {
    list-style-type: square;
}


/**
 * Headings
 */
h1 {
    font-size: 18px;
}

h2,
h3,
h4,
h5,
h6 {
    font-size: 16px;
}


/**
 * Links
 */

a {
    color: #69241B;
    text-decoration: none;
    transition: border .5s;
}

a:hover {
    border-bottom: 2px dotted #000;
}


.post-content h2:before {
    content: "¶ "; // ⸿
    margin-right: 20px;
    color: #ba3e44;
}

.link {
    padding: 2px;
    color: #69241B;
    text-decoration: none;
    transition: background-color .6s;
}

.link:hover {
    background-color: #e8ebec;
    border: none;
}


/**
 * Share
 */

.share {
    margin-bottom: 30px;
    border-top: 1px solid #eee;
    color: #777;
    font-size: 17px;
}

.share a {
    border-bottom: none;
}

.twitter-share {
    color: #1B95E0;
}

.facebook-share {
    color: #3A5795;
}

.googleplus-share {
    color: #E53935;
}


/**
 * Blockquotes
 */
blockquote {
    border-left: 5px solid #a3b1b6;
    margin-left: 30px;
    padding: 0px 10px;
    color: #00567b;
}


/**
 * Hr
 */
hr {
    border: none;
    height: 1px;
    margin: 40px auto;
    background: #5d9ea0;
    width: 100%;
}



/**
 * Code formatting
 */
pre,
code {
    font-size: 15px;
    border: 1px solid #e8ebec;
    border-radius: 3px;
    background-color: #e8ebec;
    color: #150705;
}

code {
    padding: 1px 5px;
}

pre {
    padding: 8px 12px;
    overflow-x: auto;

    > code {
        border: 0;
        padding-right: 0;
        padding-left: 0;
    }
}


/**
 * Wrapper
 */
.wrapper {
    max-width: -webkit-calc(#{$content-width} - (#{$spacing-unit} * 2));
    max-width:         calc(#{$content-width} - (#{$spacing-unit} * 2));
    margin-right: auto;
    margin-left: auto;
    padding-right: $spacing-unit;
    padding-left: $spacing-unit;
    @extend %clearfix;

    @include media-query($on-laptop) {
        max-width: -webkit-calc(#{$content-width} - (#{$spacing-unit}));
        max-width:         calc(#{$content-width} - (#{$spacing-unit}));
        padding-right: $spacing-unit / 2;
        padding-left: $spacing-unit / 2;
    }
}



/**
 * Clearfix
 */
%clearfix {

    &:after {
        content: "";
        display: table;
        clear: both;
    }
}



/**
 * Icons
 */
.icon {

    > svg {
        display: inline-block;
        width: 16px;
        height: 16px;
        vertical-align: middle;

        path {
            fill: $grey-color;
        }
    }
}




/**
 * TBC
 */

.tbc {
    height: 1px;
    margin: 6rem auto;
    width: 100%
}

.tbc:before {
    content: "- T.B.C -";
    width: 100%;
    text-align: center;
    display: block;
    color: #00567b;
    letter-spacing: 4px;
    position: relative;
    top: -8px;
}


/**
 * EOF
 */
.eof {
    height: 1px;
    margin:6rem auto;
    width: 100%;
}

.eof:before {
    content: "- E.O.F -";
    width: 100%;
    text-align: center;
    display: block;
    color: #812b2f;
    letter-spacing: 4px;
    position: relative;
    top: -8px;
}

/**
 * top-nav - new
 */

.topnav {
  overflow: hidden;
  text-align: center;
  background-color: #333;
}

.topnav a {
  float: left;
  color: #f04c3a;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  font-size: 17px;
}

.topnav a:hover {
  background-color: #ddd;
  color: black;
}

.topnav a.active {
  background-color: #4CAF50;
  color: white;
}

/**
 * Evidence
 */
.evidence {
    background-image: -webkit-linear-gradient(top, rgba(255,240,70,0.7), rgba(255,240,70,0.7));
    background-image: linear-gradient(to bottom, rgba(255,240,70,0.7), rgba(255,240,70,0.7));
    color: #222;
}
